:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 20%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 28%, 25%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 95%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(227, 47%, 91%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

body.light {
  --background: var(--light-bg);
  --background-half: var(--light-top-bg);
  --card-bg: var(--light-card);
  --text-color: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-hover: var(--light-card-hover);
  --toggle-bg: var(--toggle-bg-light);
}

body.dark {
  --background: var(--dark-bg);
  --background-half: var(--dark-top-bg);
  --card-bg: var(--dark-card);
  --text-color: var(--dark-text1);
  --text-color2: var(--dark-text2);
  --card-hover: var(--dark-card-hover);
  --toggle-bg: linear-gradient(
    45deg,
    var(--toggle-bg-start),
    var(--toggle-bg-end)
  );
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
}
body::before {
  content: "";
  width: 100%;
  height: 34%;
  position: absolute;
  border-bottom-left-radius: 1.25rem;
  border-bottom-right-radius: 1.25rem;
  overflow: hidden;
  top: 0;
  left: 0;
  z-index: -1;
  background: var(--background-half);
}
@media (max-width: 39.9375em) {
  body:before {
    height: 40%;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5625rem;
  margin-bottom: 1.5625rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.container-two {
  width: 100%;
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title h1 {
  color: var(--text-color2);
}
@media (max-width: 39.9375em) {
  .header__title {
    width: 100%;
    margin-bottom: 1.5625rem;
    position: relative;
  }
  .header__title::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 0.05rem;
    background: var(--dark-text1);
    bottom: -1.5625rem;
    left: 0;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color);
}

.toggle {
  display: flex;
  border: none;
  margin: 0;
  margin-top: 1.25rem;
  padding: 0%;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
@media (max-width: 39.9375em) {
  .toggle {
    width: 100%;
    justify-content: space-between;
  }
}
.toggle p {
  font-weight: 700;
  font-size: 0.875rem;
  cursor: pointer;
  margin-right: 1.875rem;
}
.toggle__switch {
  position: relative;
  display: inline-block;
  width: 3.75rem;
  height: 2.125rem;
}
.toggle__switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.toggle__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--toggle-bg);
  border-radius: 2.125rem;
  transition: 0.35s ease-in-out;
}
.toggle__slider::before {
  position: absolute;
  content: "";
  height: 1.625rem;
  width: 1.625rem;
  right: 0.25rem;
  bottom: 0.25rem;
  background-color: var(--background);
  border-radius: 50%;
  transition: 0.35s ease-in-out;
}

body.dark .toggle__slider::before {
  transform: translateX(-1.625rem);
}

.cards {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: 1fr;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--card-bg);
  color: var(--text-color);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card::before {
  content: "";
  height: 0.3125rem;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0%;
}
.card--facebook::before {
  background: var(--facebook);
}
.card--twitter::before {
  background: var(--twitter);
}
.card--instagram::before {
  background: linear-gradient(225deg, var(--instagram-start), var(--instagram-middle) 50.91%, var(--instagram-end) 100%);
}
.card--youtube::before {
  background: var(--youtube);
}
.card__platform {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.75rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-color2);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.625rem;
}
.card__lable {
  color: var(--dark-text1);
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--bright-red);
}
.card__num {
  margin-left: 0.25rem;
}

.card2 {
  background: var(--card-bg);
  color: var(--text-color);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 1.75rem;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.card2:hover {
  background: var(--card-hover);
}
.card2__one {
  font-weight: 700;
  font-size: 0.75rem;
}
@media (min-width: 40em) {
  .card2__one {
    font-size: 0.9375rem;
  }
}
.card2__two {
  justify-self: right;
}
.card2__three {
  font-size: 1.625rem;
  font-weight: 700;
  color: var(--text-color2);
  align-self: end;
}
.card2__four {
  align-self: end;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.card2__four div {
  font-size: 0.75rem;
  font-weight: 700;
  margin-left: 0.25rem;
}
.card2__four--up {
  color: var(--limegreen);
}
.card2__four--down {
  color: var(--bright-red);
}